<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="iconMode" @click="changeMode"></i>
            <span class="text">{{modeText}}</span>
            <span class="clear" @click="showConfirm"><i class="icon-clear"></i></span>
          </h1>
        </div>
        <scroll :refreshDelay="refreshDelay" ref="listContent" class="list-content" :data="sequenceList">
          <transition-group name="list" tag="ul" ref="list">
            <li :key="item.id" ref="listItem" class="item" v-for="(item, index) in sequenceList" @click="selectItem(item, index)">
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{item.name}}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteSong(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <div class="list-operate">
          <div class="add" @click="addsong">
            <i class="icon-add"></i>
            <span class="text">添加歌曲到队列</span>
          </div>
        </div>
        <div  class="list-close" @click="hide">
          <span>关闭</span>
        </div>
      </div>
      <confirm @confirm="confirmClear" ref="confirm" :title='title'></confirm>
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from '@/base/scroll/scroll'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playMode} from 'common/js/config'
  import Confirm from '@/base/confirm/confirm'
  import {playerMixin} from 'common/js/mixin'
  import AddSong from 'components/add-song/add-song'
  export default {
    mixins: [playerMixin],
    data() {
      return {
        showFlag: false,
        title: '是否清空播放列表',
        refreshDelay: 100
      }
    },
    computed: {
      modeText() {
        if (this.mode === playMode.sequence) {
          return '顺序播放'
        } else if (this.mode === playMode.random) {
          return '随机播放'
        } else {
          return '单曲播放'
        }
      },
      ...mapGetters([
        'sequenceList',
        'currentSong',
        'playList',
        'mode'
      ])
    },
    methods: {
      addsong() {
        this.$refs.addSong.show()
      },
      show() {
        this.showFlag = true
        setTimeout(() => {
          this.$refs.listContent.refresh()
        }, 20)
      },
      hide() {
        this.showFlag = false
      },
      getCurrentIcon(item) {
        if (item.id === this.currentSong.id) {
          return 'icon-play'
        }
        return ''
      },
      selectItem(item, index) {
        if (this.mode === playMode.random) {
          index = this.playList.findIndex((song) => {
            return item.id === song.id
          })
        }
        this.setCurrentIndex(index)
        this.setPlayingState(true)
      },
      scrollToCurrent(current) {
        const index = this.sequenceList.findIndex((song) => {
          return song.id === current.id
        })
        this.$refs.listContent.scrollToElement(this.$refs.listItem[index], 300)
      },
      deleteSong(item) {
        this.deleteCurrSong(item)
        if (!this.playList.length) {
          this.hide()
        }
      },
      showConfirm() {
        this.$refs.confirm.show()
      },
      confirmClear() {
        this.clearPlayList()
        this.hide()
      },
      ...mapMutations({
        setCurrentIndex: 'SET_PLAY_INDEX',
        setPlayingState: 'SET_PLAYING_STATE'
      }),
      ...mapActions([
        'deleteCurrSong',
        'clearPlayList'
      ])
    },
    components: {
      Scroll,
      Confirm,
      AddSong
    },
    watch: {
      currentSong(newval, oldval) {
        if (newval.id === oldval.id || !this.showFlag) {
          return
        }
        this.scrollToCurrent(newval)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/variable'
  @import '~common/stylus/mixin'
  .playlist
    position: fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    z-index: 200
    background: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      trnasition: all 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        trsnaform: translate3d(0, 100%, 0)
    .list-wrapper
      position:absolute
      left: 0
      bottom: 0
      width: 100%
      background: $color-highlight-background
      .list-header  
        position:relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items:center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow:hidden
        .item
          display:flex
          align-items:center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.3s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>


