<template>
  <div id="progress-bar" ref="progressBar" @click="progressClick">
  	<div class="bar_inner">
  		<div class="progress" ref="progress"></div>
  		<div class="progress-btn-wrapper" ref="progressBtn" @touchstart="progressTouchStart" @touchmove="progressTouchMove" @touchend="progressTouchEnd">
  			<div class="progress-btn"></div>
  		</div>
  	</div>
  </div>
</template>

<script type="text/ecmascript-6">
const progressBtnWidth = 16
export default {
  created() {
    this.touch = {}
  },
  props: {
    percent: {
      type: Number,
      default: 0
    }
  },
  methods: {
    progressTouchStart(e) {
      this.touch.initiated = true
      this.touch.startX = e.touches[0].pageX
      this.touch.left = this.$refs.progress.clientWidth
    },
    progressTouchMove(e) {
      if (!this.touch.initiated) {
        return
      }
      const diffa = e.touches[0].pageX - this.touch.startX
      const offsetWidth = Math.min(this.touch.left + diffa, this.$refs.progressBar.clientWidth - progressBtnWidth)
      this._offset(offsetWidth)
    },
    progressTouchEnd(e) {
      this.touch.initiated = false
      this._tiggerProgressBar()
    },
    _tiggerProgressBar() {
      const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
      const currentWidth = this.$refs.progress.clientWidth
      const currentPercent = currentWidth / barWidth
      this.$emit('progressChange', currentPercent)
    },
    progressClick(e) {
      const rect = this.$refs.progressBar.getBoundingClientRect()
      const offsetWidth = e.pageX - rect.left
      // 当我们点击progressBtn时，所获取到的offsetX是不对的
      this._offset(offsetWidth)
      this._tiggerProgressBar()
    },
    _offset(offsetWidth) {
      this.$refs.progress.style.width = offsetWidth + 'px'
      this.$refs.progressBtn.style.transform = `translate3d(${offsetWidth}px, 0, 0)`
    }
  },
  watch: {
    percent(newval) {
      if (newval >= 0 && !this.touch.initiated) {
        const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
        const offsetWidth = barWidth * newval
        this._offset(offsetWidth)
      }
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus'>
	@import '~common/stylus/variable'
	#progress-bar
		height: 30px
		.bar_inner
			position: relative
			top: 13px
			height: 4px
			background: rgba(0,0,0,0.3)
			.progress
				position: absolute
				height: 100%
				background: $color-theme
			.progress-btn-wrapper
				position: absolute
				left: -8px
				top: -13px
				width: 30px
				height: 30px
				.progress-btn
					position: relative
					top: 7px
					left: 7px
					box-sizing: border-box
					width: 16px
					height: 16px
					border: 3px solid $color-text
					border-radius: 50%
					background: $color-theme
</style>



